<template>

    <table>
      <tr
        v-for="cartItem in this.$store.state.cart.items"
        :key="cartItem.book.bookId"
      >
        <td class="bookTitleCSS">{{ cartItem.book.title }}</td>
          <td class="bookPriceCSS">{{ cartItem.book.price | currency }}</td>
        <td class="bookQty">
          <button @click="update(cartItem.book, cartItem.quantity - 1)">
            -
          </button>
          {{ cartItem.quantity }}
          <button @click="update(cartItem.book, cartItem.quantity + 1)">
            +
          </button>
        </td>
      </tr>
    </table>

</template>

<script>
export default {
  name: "CartTable",
  methods: {
    update(book, quantity) {
      this.$store.dispatch("updateCart", { book, quantity });
    }
  }
};
</script>

<style scoped>
table {
    margin: 0px auto;

    margin-top: 10px;
    padding-bottom: 10px;
    border-radius: 25px;
}

td {
  border: 1px solid black;
  text-align: left;
  padding: 8px;
    font-size: 25px;
    font-family: "Calibri", sans-serif;
    font-weight: lighter;
}

.bookTitleCSS {
    text-decoration: underline;

}

.bookPriceCSS {
    font-weight: bold;
}
tr:nth-child(even) {
  background-color: #fff2cc;
}

tr:nth-child(odd) {
    background-color: #9dc3e6;
}

button {
  width: 25px;
    border-color: black;
}

</style>
